html{
    background-color: rgb(117, 47, 182);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-family: 62.5%;
}

body{
    padding: 60px;
    color: white;
}

input {
    border-radius: 5px;
    border: 1px solid rgba(82, 79, 79, 0.904);
    box-shadow: 0 2px 2px;
    padding: 5px;
    color: rgb(70, 64, 64);
    width: 100%;
    font-size: 1.1rem;
    height: 20px;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    background-color: white;
    color: rgb(87, 85, 85);
}

.card p{
    padding: 0 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}